	module.exports = {"data":{"markdownRemark":{"html":"<p>到怀柔的慕田裕长城关去，据说是长城比较险峻的地方，一般的人到八达岭去，而慕田裕则去得较少。</p>\n<p>怀柔过去是贫困县，现在经济发展起来。这个县呈一种长形的地形，百分之九十是山区。山区里的情况比较落后。靠近长城的时候，有一种深切的感受，就是这里自然环境保护得特别好，绿色的植被郁郁葱葱，连绵不断，令人心旷神怡。在县委，他们说，今年县委抓了一件事情，通过社会和政府支持，把山区里的小学生都集中起来上学，基本上消灭了零散的山区学校。抓教育是根本大事。</p>\n<p>到慕田裕长城，坐缆车上去。的确比较险峻，面貌保留得也比较原汁原味，山上飘扬着旌旗，一些装成古代士兵的农民站在上面，古代色彩浓厚。可惜大雾障天，不能看到远方景象，不然一定十分怡人。有点象\"长城内外，唯余茫茫。\"</p>\n<p>再上红螺寺，据说这里曾出了一位师祖，与大寺大庙相比，红螺寺名气较小。一位老方丈来做介绍，说了一大通，古今中外联系一起，改革开放念念不忘。有一句话说得大家轰堂大笑：\"对旧的东西念念不忘，对新的东西格格不入，是不行的。改革人物不就是马胜利鲁冠球等人嘛?\"</p>\n<p>和尚也开放了。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0144.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0144.md","frontmatter":{"title":"9月3日 星期六"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0144.md absPath of file >>> MarkdownRemark"}}

